import React from "react";
import infoCard from "../InfoCard/InfoCard";

const Welcome = () => {
  return (
    <div className="w-90 w-80-l center mb3">
      <h1 className="tc pl3">Vlkommen till Esmia!</h1>
      <p className="pa3 tl">
        Vi sljer spanska delikatesser. Efter 20 r i branschen kan vi bekrfta
        att det r kvalitet som sljer!
      </p>
      <p className="pa3 tl">
        Vrt lager bestr av kylvaror, ssom ibericoskinka, serranoskinka,
        chorizo, lagrad manchego, frsk getost, sherryvinger, olivolja,
        pimentos piquillo, marconamandlar med mera.
      </p>
      <p className="pa3 tl">
        Vra priser stts med lg marginal i frhllande till importpriset.
        Nedan hittar ni vra produkter.
      </p>
      <hr
        style={{
          border: "0",
          height: "1px",
          backgroundImage:
            "linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0))"
        }}
      />
    </div>
  );
};

export default Welcome;



